.createTaskForm {
    background-color: var(--backgroundColorMain);
    background-color: #FCFFC1;
    background-color: #FFFDF0;
    padding-top: 5px;
    border-bottom-right-radius: 20px;
    border-bottom-left-radius: 20px;
    margin-top: 5px;
}

@media screen and (max-width : 499px) {
    .createTaskForm {
        margin: 5px 0;
        width: 100%;
    }
}

.createTaskForm .openCreateTask {
    width: 100%;
    cursor: pointer;
    margin-top: -10px;
    border-bottom-right-radius: 20px;
    border-bottom-left-radius: 20px;
}

.createTaskForm .openCreateTask:hover {
    background-color: black;
}

.createTaskForm .openCreateTask:active {
    background-color: black;
}

.createTaskForm .openCreateTask p{
    background-color: var(--backgroundColorMain);
    background-color: #FCFFC1;
    background-color: #FFFDF0;
    border: 0;
    color: black;
    cursor: pointer;
    font-size: 20px;
    padding: 10px;
    border-bottom-right-radius: 20px;
    border-bottom-left-radius: 20px;

}

.createTaskForm .openCreateTask:hover p{
    background-color: #3C3D37;
    color: white;
    font-weight: 700;
}

.createTaskForm .openCreateTask:active p{
    background-color: black;
    color: white;
    font-weight: 700;
}

@media screen and (max-width : 499px) {
    .createTaskForm .openCreateTask p{
        font-size: 12px;
        margin-left: 5px;
    }
}

.createTaskFormMain fieldset {
    border: 3px solid grey;
    border: 0;
    border-top: 1px solid grey;
    background-color: #fff;
    border-radius: 10px;
    border-radius: 0;
    padding-bottom: 20px;
    border-bottom-right-radius: 20px;
    border-bottom-left-radius: 20px;
    background-color: #FFFDF0;
}

.createTaskFormMain legend {
    margin-left: 20px;
    font-weight: 500;
    font-size: 18px;
}
.createTaskForm .createTaskInput {
    font-size: 16px;
    margin-left: 10px;
    margin-top: 5px;
}

@media screen and (max-width : 499px) {
    .createTaskForm .createTaskInput {
        font-size: 12px;
        margin-left: 5px;
    }
}

.createTaskForm .createTaskInput label, .createTaskForm .createTaskInput span {
    font-weight: 500;
}

.createTaskForm .createTaskInput input {
    background-color: #e8e7e7;
    border: 1px #e8e7e7 solid;
    font-size: 16px;
}

.createTaskForm .createTaskInput input:focus {
    background-color: #fff;
}


@media screen and (max-width : 499px) {
    .createTaskForm .createTaskInput input {
        background-color: #e8e7e7;
        border: 1px #e8e7e7 solid;
        font-size: 12px;
    }    
}


.createTaskForm .createTaskFunction {
    margin-left: 10px;
    margin-top: 10px;
    display: flex;
}

.createTaskFunction input {
    display: none;
}

.createTaskForm .createTaskButton {
    font-size: 14px;
    margin-top: 10px;
    background-color: black;
    font-weight: bold;
    color: white;
    padding: 5px;
    border-radius: 5px;
    cursor: pointer;
    border: 0;
    position: relative;
}

.createTaskButton span {
    position: relative;
    z-index: 3;
} 

@media screen and (max-width : 499px) {
    .createTaskForm .createTaskButton {
        font-size: 12px;
        margin-top: 5px;
    }
}

.createTaskForm .createTaskButton::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    width: 0;
    transition-duration: 0.5s;
    border-radius: 5px;
}

.createTaskCancelButton label:hover::before {
    width: 100%;
    background-color: white;
} 

.createTaskCancelButton label:hover span {
    color: black;
} 

.createTaskSubmitButton {
    margin-right: 20px;
}

.createTaskSubmitButton label:hover::before {
    width: 100%;
    background-color: #118B50;
    color: white;
} 

.createTaskSubmitButton label:hover span {
    color: white;
} 
